    box-sizing: border-box;
    margin-top: 58px;
    margin-bottom: 75px;
    padding-top: 30px;
    padding-bottom: 34px;
    padding-left: 28px;
    border: 1px solid #eaeaea;
  }
}
@media (min-width: 1440px) {
  .goal__list {
    margin: 0;
    width: 570px;
  }
}
.goal__item {
  padding-top: 6px;
  padding-bottom: 6px;
  color: #222222;
}
.goal__item:not(:last-child) {
  padding-bottom: 13px;
}
.goal__item:last-child {
  padding-bottom: 19px;
}
@media (min-width: 768px) {
  .goal__item {
    font-size: 20px;
    line-height: 30px;
  }
}
.owner-contacts__title {
  margin: 0;
  width: 187px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  text-transform: none;
  padding-top: 26px;
  padding-bottom: 35px;
}
@media (min-width: 768px) {
  .owner-contacts__title {
    padding-top: 0;
    width: 512px;
    font-size: 36px;
    line-height: 36px;
    padding-bottom: 76px;
  }
}
@media (min-width: 1440px) {
  .owner-contacts__title {
    padding-left: 0;
    padding-right: 0;
    width: 516px;
  }
}
.owner-contacts__wrapper {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
}
@media (min-width: 1440px) {
  .owner-contacts__wrapper:not(:last-child) {
    margin-right: 78px;
  }
}
@media (min-width: 1440px) {
  .owner-contacts__wrapper-desktop {
    display: flex;
  }
}
.owner-contacts__label {
  display: block;
}
@media (min-width: 1440px) {
  .owner-contacts__label {
    margin-right: 20px;
    font-size: 20px;
    line-height: 30px;
  }
}
.add {
  padding-bottom: 0;
}
.add__title {
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  padding-bottom: 34px;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .add__title {
    font-size: 36px;
    line-height: 36px;
    padding-bottom: 57px;
  }
}
@media (min-width: 1440px) {
  .add__title {
    padding-bottom: 62px;
  }
}
.add__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .add__list {
    display: grid;
    grid-template-columns: 282px 282px;
    row-gap: 42px;
    column-gap: 61px;
  }
}
@media (min-width: 1440px) {
  .add__list {
    grid-template-columns: 305px 305px 305px 305px;
    margin-right: 110px;
    gap: 0;
    margin-bottom: 41px;
  }
}
.add__item {
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 46px;
  color: #000000;
}
@media (min-width: 768px) {
  .add__item {
    font-size: 20px;
    line-height: 30px;
  }
}
.add__item:not(:last-child) {
  margin-bottom: 13px;
}
@media (min-width: 768px) {
  .add__item:not(:last-child) {
    margin: 0;
  }
}
.comment__title {
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  color: #000000;
  padding-bottom: 38px;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .comment__title {
    box-sizing: border-box;
    padding-top: 62px;
    padding-bottom: 76px;
    font-size: 36px;
    line-height: 36px;
  }
}
